
import { toRefs, reactive } from 'vue'
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'leftMenu',
  props: {
    name: String,
    listData: Array
  },
  setup(props, context){
    const state = reactive({
      acIndex: 0,
      defaultProps: {
        children: 'children',
        label: 'name',
      }
    })
    const clickFn = (data) => {
      // state.acIndex = index
      context.emit('change',data)
    }
    return {
      ...toRefs(state),
      clickFn
    }
  }
});
